<template>
    <div class="information">
        <div class="content-top">
            <el-button-group class="left">
                <el-button type="default" v-bind:size="btnSize" @click="delMsgFunc()" :disabled="tableData && tableData.hasOwnProperty('list') && tableData.list.length <= 0">{{ $L('选中删除') }}</el-button>
            </el-button-group>
            <div class="right">
                <span>{{ $L('时间') }}：</span>
                <el-date-picker class="time-search"
                                style="width: 230px;"
                                v-model="SearchTimeZone"
                                type="daterange"
                                align="right"
                                v-bind:size="btnSize"
                                :start-placeholder="$L('开始日期')"
                                :end-placeholder="$L('结束日期')"
                                value-format="yyyy-MM-dd"
                                @change="SearchTimeFunc">
                </el-date-picker>
                <el-select class="search" v-model="messageType" v-bind:size="btnSize"
                           @change="changeTypeFunc">
                    <el-option
                            v-for="item in messageTypelist"
                            :key="item.type"
                            :label="item.name"
                            :value="item.type">
                    </el-option>
                </el-select>
                <el-input :placeholder="$L('输入标题')" v-bind:size="btnSize" v-model="inputValue" class="input-with-select"
                          clearable
                          @keyup.enter.native="$event.target.blur"
                          @change="searchFunc">
                    <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
            </div>
        </div>
        <div class="content" v-if="tableData && tableData.hasOwnProperty('list')">
            <el-table
                    :data="tableData.list"
                    style="width: 100%" class="table"
                    :empty-text="$L('暂无数据')"
                    :header-cell-style="{background:'#f0f1f5',color:'#666'}"
                    @selection-change="handleSelectionChange">
                <el-table-column
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        :label="$L('标题')"
                        align="left"
                        max-width="300">
                    <div slot-scope="scope" class="title" @click="getInfoFunc(scope.row.id)">
                        <img :src="scope.row.img" class="left" v-if="scope.row.img !== ''">
                        <div class="msg">
                            <p class="title">{{ scope.row.title }}</p>
                        </div>
                    </div>
                </el-table-column>
                <el-table-column
                        prop="time"
                        :label="$L('时间')"
                        sortable
                        align="center">
                </el-table-column>
                <el-table-column
                        prop="clickNum"
                        :label="$L('浏览次数')"
                        sortable
                        align="center"
                        width="150">
                </el-table-column>
                <el-table-column
                        prop="status"
                        :label="$L('状态')"
                        sortable
                        align="center"
                        width="120">
                    <div class="status" slot-scope="scope">
                        <span v-bind:class="statusFunc(scope.row.status).color">{{ statusFunc(scope.row.status).text }}</span>
                    </div>
                </el-table-column>
                <el-table-column
                        :label="$L('管理')"
                        align="center">
                    <div class="manage-btn" slot-scope="scope">
                        <el-button class="btn" type="primary" @click="addMsgFunc(scope.row.id)">{{ $L('修改') }}</el-button>
                        <el-button class="btn" type="default" @click="delMsgFunc(scope.row.id)">{{ $L('删除') }}</el-button>
                    </div>
                </el-table-column>
            </el-table>
            <!-- 分页 -->
            <div class="list-pages" v-if="tableData.list !== null && tableData.allNum>0">
                <el-pagination
                        background
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-sizes="[10,20,30,40]"
                        :page-size="pageSize"
                        layout="prev, pager, next, sizes, jumper"
                        :total="tableData.allNum">
                </el-pagination>
            </div>
        </div>
		<div class="time" v-if="showTime">
			<calendar @change="onChange"/>
			<inlineCalendar />
		</div>
    </div>
</template>

<script>
    export default {
        name: "informationList",
        data() {
            return {
                loading: true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                btnSize: "small",
                messageType: 0,
                inputValue: '',
                msgArr: [],
                messageTypelist: [
                    {
                        type: 0,
                        name: this.$L('全部状态')
                    },
                    {
                        type: 2,
                        name: this.$L('未审核')
                    },
                    {
                        type: 1,
                        name: this.$L('已审核')
                    },
                ],
                SearchTimeZone: "",
                currentPage: 1,
                pageSize: 10,
                tableData: {},
                type: 0,
                dtCreateStart: '',
                dtCreateEnd: '',
				showTime:false

            }
        },
        created() {
            this.getData();
        },
        methods: {
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            getData() {
                const _self = this;
                _self.loading = true;
                let arr = {
                    p: _self.currentPage,
                    num: _self.pageSize,
                    type: _self.type,
                    keyword: _self.inputValue,
                    dtCreateStart: _self.dtCreateStart,
                    dtCreateEnd: _self.dtCreateEnd
                };
                _self.$api('getReleaseList', arr, function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.loading = false;
                            _self.showError(_self.$L('登录超时'), _self.$L('请重新登录'), 'error');
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            if (action === 'cancel') {
                                _self.$router.push({name: 'home'});
                            } else {
                                _self.loading = false;
                                _self.showError(_self.$L('网络请求失败'), _self.$L('请刷新页面重试'), 'error');
                            }
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        if (action === 'cancel') {
                            _self.$router.push({name: 'home'});
                        } else {
                            _self.loading = false;
                            _self.showError(_self.$L('网络请求失败'), _self.$L('请刷新页面重试'), 'error');
                        }
                    });
                });
            },
            getInfoFunc(id) {
                this.$router.push({name: 'informationInfo', params: {id: id}});
            },
            changeTypeFunc(val) {
                this.type = val;
                this.getData();
            },
            searchFunc(val) {
                this.inputValue = val;
                this.getData();
            },
            SearchTimeFunc(time) {
                if (time !== null) {
                    this.dtCreateStart = time[0];
                    this.dtCreateEnd = time[1];
                } else {
                    this.dtCreateStart = '';
                    this.dtCreateEnd = '';
                }
                this.getData();
            },
            handleCurrentChange(val) {
                this.currentPage = val;
                this.getData();
            },
            handleSizeChange(val) {
                this.pageSize = val;
                this.getData();
            },
            handleSelectionChange(val) {
                let arr = [];
                for (let i in val) {
                    arr[i] = val[i].id;
                }
                this.msgArr = arr;
            },
            statusFunc(value) {
                let x;
                switch (value) {
                    case 0:
                        x = {text: this.$L('未审核'), color: 'font-red'};
                        break;
                    default:
                        x = {text:  this.$L('已审核'), color: 'font-grey'};
                        break;
                }
                return x;
            },
            delMsgFunc(id) {
                const _self = this;
                if (!id && (Array.isArray(this.msgArr) && this.msgArr.length === 0)) {

                    //未知错误
                    _self.$confirm(_self.$L('您还没有选中发布的信息，请勾选发布的信息再做此操作'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('取消'),
                        type: 'warning',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(() => {

                    });
                    return;
                }

                let groupId;

                if (id) {
                    groupId = id;
                } else {
                    groupId = this.msgArr;
                }

                let arr = {
                    id: groupId,
                };
                console.log(arr);
                _self.$confirm(_self.$L('此操作将永久删除该发布信息, 是否继续?'), _self.$L('提示'), {
                    confirmButtonText: _self.$L('确定'),
                    cancelButtonText: _self.$L('取消'),
                    type: 'warning'
                }).then(() => {
                    _self.$api('deleteReleaseNews', arr, function (reData) {
                        if (reData.flag === 1) {
                            _self.loading = false;
                            _self.$message({
                                type: 'success',
                                message: _self.$L('删除成功!')
                            });
                            _self.getData();
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            console.log(reData);
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.$router.push({name: 'home'});
                            });
                        }
                    }, function (error) {
                        console.log(error);
                        //网络请求错误
                        _self.$confirm(_self.$L(error.msg), _self.$L('出错了'), { //todo:msg还未更换语言包语言包
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    });
                }).catch(() => {

                });
            },
            addMsgFunc(id) {
                if (id) {
                    this.$router.push({name: 'informationEdit', params: {id: id}});
                } else {
                    this.$router.push({name: 'informationAdd'});
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/common";
    @import "../../../assets/css/common/lib";
    .information {
        position: absolute;
        width: 100%;
        height: auto;
        top: 0;
        left: 0;
        padding: 20px;

        .information-head {
            width: calc(100% - 200px);
            position: fixed;
            top: 50px;
            height: 50px;
            z-index: 999;
            margin: 0 -20px;
            background-color: #fff;

            .left {
                float: left;
                display: inline-block;
                width: 50%;
                margin-left: 20px;
            }

            .right {
                float: right;
                margin-right: 20px;
                height: 35px;
                line-height: 35px;
                padding: 0;
                width: 90px;
                margin-top: 7px;
            }
        }

        .content-top {
            height: 32px;
            margin-bottom: 10px;
            .left {
                float: left;
            }
            .right {
                float: right;

                .time-search {
                    vertical-align: bottom;
                }

                .font {
                    padding-left: 20px;
                    font-size: 16px;
                }

                .search {
                    width: 120px;
                    padding-left: 10px;
                }

                .input-with-select {
                    width: 260px;
                    padding-left: 10px;
                }
            }
        }

        .content {
            padding: 20px;
            background: #fff;

            .table {
                width: 100%;
                .manage-btn {
                    .btn {
                        padding: 8px 30px;
                    }
                }

                .title {
                    height: 50px;
                    cursor: pointer;

                    .left {
                        width: 50px;
                        height: 50px;
                        float: left;
                        margin-right: 20px;
                    }

                    .msg {
                        float: left;
                        width: calc(100% - 70px);
                        line-height: 50px;

                        .title {
                            color: #333;
                            text-overflow: ellipsis;
                            white-space: nowrap;
                            overflow: hidden;
                        }
                    }
                }

                .status {
                    .font-red {
                        color: #ff0000;
                    }

                    .font-grey {
                        color: #999;
                    }
                }
                /deep/ .el-table__empty-block {
                    @extend .no-select;
                    position: relative;
                    width: 100%;
                    height: 380px;
                    line-height: 380px;
                    text-align: center;
                    background-color: #ffffff;
                    font-size: 16px;
                    color: #999;
                    margin-bottom: 20px;
                }
            }

            .list-pages {
                text-align: center;
                padding: 40px 0 20px;
            }
        }
		.time{
			width: 100%;
			position:fixed;
		}
        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
    }
</style>